<template>
  <div>
    <layout-box one="角色管理" two="分配菜单">
      <div style="margin-top: 20px">
        <el-tree
          ref="tree"
          align="center"
          :data="menuTreeList"
          :props="defaultProps"
          node-key="id"
          show-checkbox
          highlight-current
          default-expand-all
          :default-checked-keys="defKeys"
        ></el-tree>
      </div>
      <div style="margin-top: 20px" align="center">
        <el-button type="primary" @click="handleSave()">保存</el-button>
        <el-button @click="handleClear()">清空</el-button>
      </div>
    </layout-box>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defKeys: [],
      queryInfo: {
        pageNo: 1,
        pageSize: 10
      },

      defaultProps: {
        children: 'childList',
        label: 'menuName'
      },
      menuTreeList: [],
      roleId: null,
      id: null,
      jurisdiction: [
        { roleId: '', menuId: '' }
      ],
    }
  },
  created() {
    this.roleId = this.$route.query.roleId
    this.fetch()
    this.treeList()
  },
  methods: {
    // 获取菜单
    async treeList() {
      const res = await this.$http.post('/woho/sys/menu/treeMenu', this.id)
      this.menuTreeList = res.data.data
    },
    // 回显菜单
    async fetch(roleId) {
      const res = await this.$http.post(`/woho/sys/menu/treeMenuByRoleId/${this.roleId}`)
      const menuList = res.data.data
      if (menuList != null && menuList.length > 0) {
        for (let i = 0; i < menuList.length; i++) {
          this._childList(menuList[i])
        }
      }
    },
    //  清空
    handleClear() {
      this.$refs.tree.setCheckedKeys([])
    },
    // 分配菜单
    async handleSave(roleId) {
      let arr1 = this.$refs.tree.getCheckedKeys()
      // eslint-disable-next-line no-unused-vars
      let arr2 = this.$refs.tree.getHalfCheckedKeys()
      // eslint-disable-next-line no-unused-vars
      let arr = [...arr1, ...arr2]
      let aaa = arr.map((item) => {
        return {
          'roleId': this.roleId,
          'menuId': item
        }
      })
      this.jurisdiction = aaa
      const res = await this.$http.post('/woho/sys/menuRole/insert', this.jurisdiction)
      if (res.data.code !== 200) {
        this.$message.error('分配失败')
      } else {
        this.$router.push('/role')
        this.$message.success('分配成功')
      }
    },

    // 私有方法--------------------------------------------------------------------------------
    // 递归获取最底层的childList
    _childList (data) {
      if (data.childList.length === 0) {
        this.defKeys = [data.id, ...this.defKeys]
      } else {
        return data.childList.map((item) => {
          return this._childList(item)
        })
      }
    },
  }
}
</script>

<style></style>
